*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }


  h2{
      text-align: left;
      margin-left: 0;
  }

  .title{
    text-align: left;
    margin: 0;
  }
  
  header{
    background-color: lightgreen;
    font-size: 35px;
    text-align: center;
    padding: 10px;
    font-weight: bold;
    display: block;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  }
  
  
  .foodCard{
    width: 620px;
    height: 540px;
    margin: 0 auto;
    padding: 10px;
    margin-top: 15px;
    overflow-y: auto;
    overflow-x: hidden;
    box-shadow: 2px 3px 9px -1px #000;
  }
  
  .item{
    border-radius: 5px;
    margin: 15px;
    font-weight: bold; 
    box-shadow: 5px 2px 3px grey;
    padding: 12px;
    color: white;
    background-color: darkgreen     ;  
  }

 

